"use client";

import * as React from "react";
import {
  Home,
  Settings,
  Users,
  FileText,
  BarChart3,
  HelpCircle,
  LogOut,
  School,
  Receipt,
  UserCog,
} from "lucide-react";
import { Button } from "@/components/ui/button";

import SidebarItem from "@/components/userStudent/components/Sidebaritem";
import Card from "@/components/userStudent/components/card";
import DataTable from "@/components/userStudent/components/Table";

export default function SidebardAdmin() {
  return (
    <div className="grid min-h-screen w-full grid-cols-[250px_1fr]">
      <AppSidebar />
      <main className="flex flex-col">
        <header className="sticky top-0 flex h-14 items-center gap-4 border-b bg-white px-4 shadow-sm">
          <h1 className="text-lg font-semibold">Dashboard Admin</h1>
        </header>
        <div className="flex-1 p-4 md:p-6">
          <div className="mx-auto grid max-w-6xl gap-6">
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
              <Card title={"Estadísticas"} description={"Visión general del sistema"} Icon={BarChart3} />
              <Card title={"Usuarios"} description={"Gestión de usuarios"} Icon={Users} />
            </div>
            <DataTable />
          </div>
        </div>
      </main>
    </div>
  );
}

function AppSidebar() {
  return (
    <aside className="h-screen w-[250px] bg-blue-500 text-white p-4 flex flex-col">
      <div className="flex items-center gap-2 mb-6">
        <div className="size-8 rounded-full bg-green-600 flex items-center justify-center text-white font-bold">
          A
        </div>
        <div>
          <div className="text-sm font-semibold">CEPRE UNAJMA</div>
          <div className="text-xs">Administrador</div>
        </div>
      </div>
      <nav className="flex-1">
        <ul className="space-y-2">
          <SidebarItem icon={Home} label="Panel" active />
          <SidebarItem icon={Users} label="Gestión de Usuarios" active={undefined} />
          <SidebarItem icon={Receipt} label="Pagos y Reportes" active={undefined} />
          <SidebarItem icon={FileText} label="Exámenes" active={undefined} />
          <SidebarItem icon={UserCog} label="Configuraciones" active={undefined} />
        </ul>
      </nav>
      <div className="mt-auto">
        <SidebarItem icon={Settings} label="Ajustes" active={undefined} />
        <SidebarItem icon={HelpCircle} label="Ayuda" active={undefined} />
        <div className="border-t border-gray-700 mt-4 pt-4">
          <div className="flex items-center gap-2">
            <div className="size-8 bg-gray-600 rounded-full" />
            <div>
              <div className="text-sm font-medium">Admin</div>
              <div className="text-xs text-gray-400">admin@example.com</div>
            </div>
            <button className="ml-auto text-gray-400 hover:text-white">
              <LogOut className="size-5" />
            </button>
          </div>
        </div>
      </div>
    </aside>
  );
}
